.timeline {
  position: relative;
  &::after {
    content: "";
    position: absolute;
    top: 9px;
    left: 0;  
    right: 0;
    height: 2px;
    background-color: $light;
  }
  &-pointer {
    position: relative;
    color: $light;
    p {
      padding-top: 24px;
      margin-bottom: 0;
      line-height: 1.5;
    }
    &::before {
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 20px;
      height: 20px;
      border: 1px solid $light;
      border-radius: 50%;
      background-color: $gray-600;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 12px;
      z-index: 1;
    }
    &.step-1::before {
      content: '1';
    }
    &.step-2::before {
      content: '2';
    }
    &.step-3::before {
      content: '3';
    }
    &.active {
      color: $primary;
      &::before {
        color: $white;  
        background-color: $primary;
        border-color: $primary;
      }
    }
  }
}

.pseudo-triangle {
  position: relative;
  &::after {
    background-color: inherit;
    position: absolute;
    content: "";
    left: 50%;
    bottom: -12px;
    transform: translateX(-50%);
    width: 24px;
    height: 12px;
    clip-path: polygon(0% 0%, 100% 0%, 50% 100%);
  }
}